.lms_category_wrap{
    margin-bottom: 50px;
    .category_header{
        background: $section_bg;
        border-radius: 5px;
        padding: 17px 30px;
        .category_left{
            h4{
                font-size: 16px;
                font-weight: 500;
                color: #222;
                margin-bottom: 0;
            }
            p{
                color: $text_color;
                font-size: 12px;
                font-weight: 300;
                margin-bottom: 0;
            }
        }
        .category_right{
            @media #{$small_mobile} {
                margin-top: 15px;
            }
        }
    }
    ul{
        li{
            display: flex;
            justify-content: space-between;
            position: relative;
            padding: 15px 30px;
            background: #fff;
            align-items: center;
            border-bottom: 1px solid #eee1e2;
            transition: .3s;
            @media #{$small_mobile} {
                padding: 15px 20px;
            }
            .category_info{
                p{
                    font-size: 13px;
                    font-weight: 300;
                    color: $text_color;
                }
            }
            &:hover{
                background: #fff7f8;
            }
        }
    }
}

.income_servay{
    .count_content{
        text-align: center;
        margin-bottom: 30px;
        h3{
            font-size: 30px;
            font-weight: 600;
            color: #2E4765;
            line-height: 1;
            margin-bottom: 5px;
            span{
                color: #2E4765; 
                font-weight: 600;
                line-height: 1;
            }
        }
        p{
            font-size: 14px;
            font-weight: 400;
            color: #B2B5C0;
            line-height: 1;
        }
    }
}

.staf_list_wrapper{
    .single_staf{
        background: #fff;
        box-shadow: 0  7px 15px rgba(80, 143, 244,.15);
        text-align: center;
        padding: 50px 0;
        margin-bottom: 30px;
        margin-top: 30px;
        .staf_thumb{
            margin-bottom: 25px;
            img{
                margin: auto;
                width: 118px;
                height: 118px;
                @include border-radius(50%);
            }
        }
        h4{
            font-size: 22px;
            font-weight: 600 ;
            color:#2E4765 ;
            margin-bottom: 3px;
        }
        p{
            font-size: 14px;
            color: #818E94;
            font-weight: 500;
        }
    }
}

.owl-carousel{
    .owl-nav{
        button{
            width: 39px;
            height: 39px;
            border-radius: 7px;
            background: #EFF1F7;
            position: absolute;
            top: -89px;
            right: 0;
            font-size: 18px !important;
            background: #EFF1F7 !important;
            color: #141124;
            &.owl-prev{
                right: 50px;
            }
            &:hover{
                background: #16BBE5 !important;
                color: #fff;
            }
        }
    }
}

.Activity_timeline{
    ul{
        border-left: 1px solid #ECFDFF;
        li{
            position: relative;
            margin-bottom: 15px;
            &:last-child{
                margin-bottom: 20px;
            }
            .activity_bell{
                background: #3BB0BD;
                width: 10px;
                height: 10px;
                border-radius: 50%;
                position: absolute;
                left: -5.5px;
                top: 5px;
            }
            .activity_wrap{
                position: relative;
                padding-left: 30px;
                h6{
                    font-size: 18px;
                    font-weight: 600;
                    color: #2E4765;
                }
                p{
                    font-size: 14px;
                    line-height: 28px;
                    color: #818E94;
                    font-weight: 400;
                }
            }
        }
    }
}


.activity_progressbar{
    .single_progressbar{
        h6{
            font-size: 12px;
            text-transform: capitalize;
            margin-bottom: 5px;
        }
    }
}
.barfiller {
    width:100%;
    height:10px;
    background:$body_bg;
    position:relative;
    margin-bottom:25px;
    border-radius: 30px;
    .fill {
        display:block;
        position:relative;
        width:0px;
        height:100%;
        z-index:1;
        border-radius: 30px;
    }
    .tipWrap {
        display:none;
    }
    .tip {
        margin-top:-23px;
        padding:2px 4px;
        font-size:11px;
        color:#000;
        left:0px;
        position:absolute;
        z-index:2;
    } 
    &#bar1{
        .tip {
            color:#089bab;
        }
    }
    &#bar2{
        .tip {
            color:#28a745;
        }
    }
    &#bar3{
        .tip {
            color:#ffc107;
        }
    }
    &#bar4{
        .tip {
            color:#dc3545;
        }
    }
}

